@import url('https://fonts.googleapis.com/css2?family=Anton&family=Roboto:wght@300&display=swap');

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(12, 12, 12);
  color: black;
  font-family: 'Roboto', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: rgb(148, 148, 148);
}

h1 {
  font-family: 'Anton', sans-serif;
}

p {
  word-break: break-all;
}

span {
  color: rgb(148, 148, 148);
}

.login-container {
  display: grid;
  place-items: center;
  height: 100vh;
}

.login-box {
  align-items: center;
  background-color: rgb(15, 15, 15);
  box-shadow: 10px 10px 5px black;
  color: white;
  display: flex;
  flex-direction: column;
  height: 300px;
  width: 500px;
}

.login-box h1 {
  font-size: 50px;
}

.login-box span{
  color: rgb(0, 134, 52);
}

.login-form {
  display: flex;
  gap: 10px;
}

.login-form input {
  border-radius: 20px;
  padding-left: 10px;
  width: 250px;
}

.login-form input:focus-visible {
  outline: none;
}

.login-form button {
  background-color: rgb(0, 134, 52);
  border-radius: 30px;
  border: none;
  color:  white;
  cursor: pointer;
  font-weight: 700;
  height: 30px;
  padding: 0 20px;
  width: 100px;
}

.login-form button:disabled {
  opacity: 0.5;
}


.login-form button:hover {
 opacity: 0.8;
}

.login-loading {
  margin: 20% auto;
}

.login-loading span {
  font-size: 30px;
  color: white;
  margin-left: 50%;
}

.header-container {
  display: flex;
  justify-content: space-between;
}

h1 span {
  color: rgb(0, 134, 52);
}

.sidebar-container {
  align-items: center;
  background-color: rgb(15, 15, 15);
  box-shadow: 5px 5px 5px black;
  color: white;
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0px;
  position: fixed;
  width: 300px;
}

.sidebar-container span {
  color: rgb(0, 134, 52);
}

.sidebar-container hr {
  border-color: rgb(37, 37, 37);
  width: 90%;
}

.sidebar-container ul {
  list-style: none;
}

.sidebar-container li {
  text-decoration: none;
  color: rgb(148, 148, 148);
  font-size: 25px;
}

.sidebar-container li:hover {
  color: white;
}

.sidebar-container a:hover {
  color: white;
}

.user {
  color: white;
  font-size: 20px;
}

.user img {
  height: 40px;
  object-fit: cover;
  width: 40px;
  z-index: 1;
}

.user a {
  align-items: center;
  display: flex;
  gap: 20px;
}

.user span {
  color: rgb(148, 148, 148);
}

.search-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.search-container h2 {
  color: white;
}

.albums-search {
  display: flex;
  color: white;
  gap: 1%;
  padding-top: 30px;
}

.albums-search input {
  border-radius: 20px;
  height: 30px;
  padding-left: 10px;
  width: 250px;
}

.albums-search input:focus-visible {
  outline: none;
}

.albums-search button {
  background-color: rgb(12, 12, 12);
  border-radius: 30px;
  border: 3px solid white;
  color: white;
  height: 40px;
  padding: 0 20px;
  width: 100px;
}

.albums-search button:disabled{
  border: 3px solid rgb(100, 100, 100);
  background-color: rgb(12, 12, 12);
  color: rgb(100, 100, 100);
}

.albums-search button:hover {
  opacity: 0.9;
}

.search-result-container {
  padding: 10px;
}

.search-result-container span {
  font-size: 30px;
}

.search-result-container h4 {
  color: white;
}

.albums-container {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  margin-left: 300px;
}

.album-card {
  background-color: rgb(15, 15, 15);
  box-shadow:  12px 12px 10px black;
  color: white;
  display: flex;
  flex-direction: column;
  height: 330px;
  justify-content: space-evenly;
  margin-bottom: 5px;
  padding: 10px;
  width: 200px;
}

.album-img-and-title {
  font-size: 0.7rem;
}

.album-img-and-title img {
  width: 200px;
}

.artist-album-name {
  align-content: center;
  align-items: center;
  display: flex;
  font-weight: 700;
  justify-content: start;
  padding: 3px;
}

.album-card:hover {
  opacity: 0.7;
}

.musics-container {
  color: white;
  margin: auto;
  max-width: 800px;
}

.album-title-container {
  display: flex;
  background-color: rgb(15, 15, 15);
  box-shadow: 10px 10px 5px black;
  margin-bottom: 5px;
  width: 800px;
}

.album-title-container img {
  padding: 20px;
  width: 200px;
}

.album-title-container h1, h3 {
  padding-left: 10px;
}


.music {
  background-color: rgb(15, 15, 15);
  box-shadow:  12px 12px 10px black;
  margin-bottom: 10px;
  padding: 20px;
}

.music div{
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.music audio {
  width: 600px;
}

.music input, label {
  cursor: pointer;
}

.profile-container {
  display: grid;
  place-items: center;
}

.profile-box {
  background-color: rgb(15, 15, 15);
  box-shadow:  12px 12px 10px black;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 15px;
  width: auto;
}

.profile-box img {
  height: 350px;
  object-fit: cover;
  width: 350px;
}

.profile-box a {
  align-self: flex-end;
  background-color: rgb(0, 134, 52);
  border-radius: 30px;
  border: none;
  color:  white;
  cursor: pointer;
  font-weight: 700;
  padding: 10px;
}

.profile-box a:hover {
  opacity: 0.8;
}

.profile-edit {
  background-color: rgb(15, 15, 15);
  box-shadow:  12px 12px 10px black;
  color: white;
  display: flex;
  gap: 40px;
  margin-left: 40%;
  margin-top: 10%;
  padding: 20px;
  width: 800px;
}

.img-box {
  background-color: white;
  max-width: 200px;
  max-height: 200px;
}

.img-profile {
  height: 200px;
  object-fit: cover;
  width: 200px;
  z-index: 1;
}

.profile-edit form {
  display: flex;
  flex-wrap: wrap;
}

.profile-edit form label {
  display: flex;
  flex-direction: column;
  padding: 5px;
  vertical-align: center;
}

label span {
  color: white; 
  font-weight: 700;
}

.profile-edit form label input{
  height: 20px;
  width: 250px;
}

#input-description {
  height: 100px;
  resize: none;
}

textarea {
  width: 520px;
}

.save-edit {
  background-color: rgb(0, 134, 52);
  border-radius: 30px;
  border: none;
  color:  white;
  cursor: pointer;
  font-weight: 700;
  height: 30px;
  margin-left: 160px;
  margin-top: 25px;
  padding: 0 20px;
  width: 100px;
}

.save-edit:hover {
  opacity: 0.8;
}

.save-edit:disabled {
  opacity: 0.5;
}